import { type Dispatch, type SetStateAction } from 'react';

import { type BasicAppResources } from 'src/code.client/app_resources/basic_app_resources';
import { isTokenError } from 'src/code.client/auth/utils';
import { DXTErrorCode, DXTException, type DXTErrorCallback } from 'src/domain/shared/errors';

import { userAuthConnectRequest } from 'src/api-client/auth/requests';

import { getStoredToken } from '../auth_store.utils';
import { type AuthState, AuthStateDisconnected, AuthStateLoggedIn } from '../states';
import { AuthAction, type OnRetryCallBack } from './base';
import { type UserPublicInfo } from 'src/domain/user/entities/user';

export class AuthActionRefreshAll extends AuthAction {
  constructor(
    readonly currentUserInfo: UserPublicInfo,
    readonly errorCallback?: DXTErrorCallback
  ) {
    super();
  }

  async run(
    authState: AuthState,
    setAuthState: Dispatch<SetStateAction<AuthState>>,
    app: BasicAppResources,
    onRetryCallback: OnRetryCallBack | undefined | null
  ): Promise<void> {
    if (!(authState instanceof AuthStateLoggedIn)) throw new DXTException(DXTErrorCode.INTERNAL_SERVER_ERROR, 'Must be logged in');

    const storedToken = getStoredToken();
    if ((storedToken == null) || (authState.authToken !== storedToken)) {
      setAuthState( new AuthStateDisconnected() );
      return ;
    }

    const response = await userAuthConnectRequest(authState.authToken, app, true);

    response.map({
      error: (errorState) => {
        const apiError = errorState.error;

        if (isTokenError(apiError.status)) {
          setAuthState( new AuthStateDisconnected() );
          
        }
      },
      success: (success) => {
        const currentUser = authState.userInfo;
        const currentAuthToken = authState.authToken;

        const newUser = success.data.user;
        const newAuthToken = success.data.auth_token;

        if (
          currentAuthToken !== newAuthToken ||
          currentUser.timestamp_modificacion !== newUser.timestamp_modificacion
        ) {
          setAuthState(
            new AuthStateLoggedIn(newAuthToken, newUser)
          );
        }
      },
    });
  }
}
