// ** React Imports
import { useEffect } from 'react';

import { useLocation, useNavigate } from '@remix-run/react';

import { redirectLoginWithReturnUrl } from 'src/code.client/auth/utils';

// ** Hooks Import
import {
  AuthActionRefreshAll,
  AuthStateLoggedIn,
  useAuth,
} from './auth_context';

export const AuthGuard = (props: any) => {
  const { children } = props;

  const { state: authState, dispatch: authDispatch } = useAuth();

  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    if (authState instanceof AuthStateLoggedIn) {
      (async () => {
        await authDispatch(new AuthActionRefreshAll(authState.userInfo));
      })().catch((_) => {});
    } else {
      if (authState.isDisconnectedAndNotRedirecting()) {
        redirectLoginWithReturnUrl(navigate);
      }
    }
  }, [location, authState.constructor.name]);

  if (authState.isLoggedIn()) {
    return <>{children}</>;
  }
};
