import { UserRole } from "../types";
import { USER_ROLE_CUSTOMER, USER_ROLE_VENDOR, USER_ROLE_ADMIN } from "src/texts/users";


export function getUserRoleText(role: UserRole): string {
  switch (role){
    case UserRole.customer: return USER_ROLE_CUSTOMER;
    case UserRole.vendor: return USER_ROLE_VENDOR;
    default: return USER_ROLE_ADMIN;
  }
}
